<template>
	<view class="loginpage bg-white">


		<!--title-->
		<view style=" padding-top: 20upx;">
		<view @tap="gotomy()" style="padding-bottom: 60upx;margin-left: 15upx;">
			<image src="../../static/icon/back.png" style="width: 60upx;height: 60upx;"></image>
		</view>
		
		<view class="title">
			<view style="font-size: 300%;font-family:'Lufcida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;">
				請上傳</view>
			<text class="login">身份證照</text>
		</view>
	</view>

		<!--input box-->
<!-- 		<view class="input-fa">

			<view style="margin-top: 50upx;" class="flex justify-center">
				<button class="bg-gradual-orange" style="width: 450upx;" type="warn" @click="uploadFile">Upload</button>
			</view>

		</view> -->

	<view style="margin: 40upx;" class="flex justify-center">
		<button @click="uploadFile" class="bg-gradual-blue round" style="width: 650upx;">
			上傳圖片
		</button>
	</view>

	</view>
</template>

<script>
	export default {

		data() {

			return {
				form: {
					id: "",
					sex: "",
					birthday: "",
					userInfo: "",
					
				},

			}
		},
		methods: {	
			uploadFile: function() {
				uni.chooseImage({
					count: 1,
					sizeType: ['original', 'compressed'],
					sourceType: ['album', 'camera'],
					success: function(res) {
						// uni.showToast({
						// 	title: '提交成功',
						// 	duration: 2000
						// })
						uni.previewImage({
							 urls: res.tempFilePaths,
						})
						var tempFilePaths = res.tempFilePaths;
						var Proimgurl = res.tempFilePaths[0];
						uni.uploadFile({
							url: "http://127.0.0.1:5000/upload", // 后端api接口
							filePath: res.tempFilePaths[0], // uni.chooseImage函数调用后获取的本地文件路劲
							name: 'file', //后端通过'file'获取上传的文件对象
							formData: {
								// imgurl:res.tempFilePaths[0],
								
							},
							// header: {
							// 	"Content-Type": "multipart/form-data"
							// },
							success: (res) => {
								var arr = [];
								arr = res.data.split(",");
								console.log(arr[0]);
								console.log(arr[1]);
								console.log(arr[2]);
								console.log(arr[3]);
								console.log(arr[4]);
								console.log(arr[5]);

							}
						});


						;

					},
					fail: function(err) {
						console.log(err)
					}

				})
			},

			gotomy() {
				uni.switchTab({
					url: "../my/my"
				})
			},
				// onShow() {
				// 	var userInfo = this.getGlobalUser("globalUser")
					
				// 	if (userInfo != null) {
				// 		// this.userIsLogin = true;
				// 		this.userInfo = userInfo;
				// 	} else {
				// 		// this.userIsLogin = false;
				// 		this.userInfo = {};
				// 	}
				// console.log(userInfo.uid);
				// },
			onLoad(options) {

				var userInfo = this.getGlobalUser("globalUser")
				if (userInfo != null) {
					this.userIsLogin = true;
					this.userInfo = userInfo;
					this.getstatus(userInfo.uid);
				} else {
					this.userIsLogin = false;
					this.userInfo = {};
				}
				
				
			},
			getstatus(input) {
			
				uni.request({
					url: `http://127.0.0.1:5000/users/my?id=${input}`,
					method: "GET",
					success: (res) => {
						console.log(res.data)
						// this.$forceUpdate()
					}
				})
			},
			
		}
	}
</script>

